html,
body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-size: 14px;
  font-family: "Open Sans", Tahoma, sans-serif;
  color: #262c37;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  letter-spacing: 0; }

body {
  overflow: hidden; }

